<template>
  <div class="showcase-page page" :id="id" v-bind:style="{height: height}">
    <div class="showcase-container">
      <h1 class="hidden text" :id="id+'-title'">{{ title }}</h1>
      <transition-group name="showcaseitem" class="items-container" :id="id+'-items-container'">
        <ShowcaseItem 
          v-for="item in items_instance"
          :key="item.name"
          :item="item"
          :id="item.name"
          ref="showcase-item"
          :imageSrc="item.image"
          :images="item.images"
          v-on:expand="CollapseItems(true)"
          />
      </transition-group>
    </div>
  </div>
</template>

<script>
import ShowcaseItem from './ShowcaseItem'

export default {
  name: "Showcase",
  props: {
    title: String,
    id: String,
    items: Array,
    height: String
  },
  components: {
    "ShowcaseItem": ShowcaseItem
  },
  data() {
    return {
      items_instance: [],
      expanded: false,
      selectedItem: null,
    }
  },
  methods: {
    /*
    GetImageURL(path) {
      // depracated function. now using imgur for images
      // instead of storing locally
      return require("@/assets/imgs/" + path);
    },*/
    ShowItems(expansionRate) {
      if(this.expanded)
        return;

      document.getElementById(`${this.id}-title`).classList.remove("hidden");

      var i = 0;
      var expansionInterval = setInterval(() => {
        this.items_instance.push(this.items[i]);

        if(i >= this.items.length - 1) {
          clearInterval(expansionInterval);
          this.expanded = true;
        }
        i++;
      }, expansionRate);
    },
    CollapseItems(emitBackwards = false) {
      // collapses all ShowcaseItems within. 

      // when this is run, close all other showcase pages
      if(emitBackwards)
        this.$emit("SelectShowcaseItem", this.id);
      
      if(this.$refs["showcase-item"] != undefined) {
        this.$refs["showcase-item"].forEach(item => {
          item.Collapse();
        });
      }
    }
  },
};
</script>

