   mounted() {
      // load saved color mode & update page
      this.LoadColorMode();

      // dictionary ?
      var pages = this.pages;    // array of strings corresponding to ID of page html elements
      var pageHeights = [];
      var cumulativeHeights = [];

      pages.forEach((page) => {
         pageHeights.push(document.getElementById(page).clientHeight);
      });

      cumulativeHeights.push(pageHeights[0]);
      for (var i = 1; i < pageHeights.length; i++) {
         cumulativeHeights.push(pageHeights[i] + cumulativeHeights[i - 1]);
      }

      // update the 'selected' nav when scroll changes
      document.onscroll = () => {
         var scroll = window.scrollY || window.scrollTop;
         // for each page if we get to around the page, set as selected

         pages.forEach((page) => {
            /*
        var cumulativeHeight = 0;
        for(var i = 0; i < pages.indexOf(page); i++) {
          cumulativeHeight += pageHeights[i];
        }*/
            // if we scroll to a certain page
            if (
               scroll >=
               cumulativeHeights[pages.indexOf(page) - 1] -
                  document.getElementById(page).clientHeight / 4
            ) {
               // remove selected if it has it
               document
                  .getElementsByClassName("selected")
                  .forEach((element) => {
                     // this removes entire list
                     // depracated
                     // element.classList.remove(...element.classList);
                     element.classList.remove("selected");
                  });
               // assign the selected nav element
               document.getElementById(`${page}-nav`).classList.add("selected");
               // add the 'visible' classlist to fade in as we scroll down
               document.getElementById(page).classList.add("visible");
               // for Showcase pages, activate their "ShowItems" function
               // takes a number as the delay between showing each
               // element in ms
               if (page == "games" || page == "apps" || page == "videos") {
                  if (!this.$refs[page].expanded) {
                     this.$refs[page].ShowItems(150);
                     this.$refs[page].expanded = true;
                  }
               }
            } else if (scroll == undefined || scroll < 100) {
               // we've reached the top
               // remove selected if it has it
               document
                  .getElementsByClassName("selected")
                  .forEach((element) => {
                     // this removes entire list
                     // depracated
                     // element.classList.remove(...element.classList);
                     element.classList.remove("selected");
                  });
               // assign the selected nav element
               document.getElementById(`home-nav`).classList.add("selected");
               // add the 'visible' classlist to fade in as we scroll down
               document.getElementById("home").classList.add("visible");
            }
         });
      };
   },
   methods: {
      ScrollToPage(_pageID) {
         document.getElementById(_pageID).scrollIntoView({
            behavior: "smooth",
         });
      },
      SelectShowcasePage(selectedPage) {
         // resets each showcase page.
         // this is used for when we click from one showcase in a category (e.g. apps)
         // to another (e.g. games)
         // it will cleanly transition between

         // also expands the right showcase page

         this.pages.forEach(page => {
            // only include pages with ref
            if (page == "games" || page == "apps" || page == "videos") {
               this.$refs[page].CollapseItems();

               // increase the page that has the selected showcase items' height
               // to fully encompass the newly increased height
               var showcaseItemHeight = 150;
               var selectedShowcaseItemHeight = 500;
               // default height;
               var currHeight = 170 * this.$refs[page].items.length;
               // expanded height
               if(page == selectedPage) {
                  currHeight += (selectedShowcaseItemHeight - showcaseItemHeight);
               }
               this.$refs[page].$el.style.height = `${currHeight}px`;
            }
         });
      },
      UpdateColorMode() {
         // updates root doc as to dark or light mode
         document.documentElement.className = this.isDarkMode ? "dark-mode" : "";
         this.$refs["dark-mode-switch"].innerHTML = this.isDarkMode ? "ðŸŒ™" : "â˜€ï¸";
      },
      ToggleColorMode() {
         // switches color modes & saves it
         this.isDarkMode = !this.isDarkMode;
         this.UpdateColorMode();
         localStorage.setItem("isDarkMode", this.isDarkMode);
      },
      LoadColorMode() {
         // load if exists
         // otherwise default to dark mode
         var darkMode = localStorage.getItem("isDarkMode");
         console.log(darkMode);
         if(!darkMode) {
            this.isDarkMode = true;
         } else {
            // local storage is saved as a string!
            this.isDarkMode = (darkMode == "true");
         }
         this.UpdateColorMode();
      }
   },
};
</script>

